import ListeLecture from './ListeLecture.js';

/**
 * Reprsente une collection de liste de lectures.
 */
export default class Bibliotheque {
    /**
     * Crer une bibliothque vide.
     */
    constructor() {
        this.listesLecture = [];
    }

    /**
     * Ajouter une nouvelle liste de lecture  la bibliothque en lui donnant un
     * nom.
     * @param {String} nom Nom de la nouvelle liste de lecture
     * @return {ListeLecture} La liste de lecture nouvellement cre
     */
    creerListe(nom) {
        let liste = new ListeLecture(nom);
        this.listesLecture.push(liste);

        return liste;
    }

    /**
     * Remplacer la liste des chansons d'une liste de lecture par une autre.
     *
     * Note : ceci est utile pour rordonner les chansons d'une liste de
     * lecture.
     * @param {Number} index Index de la liste de lecture  changer
     * @param {Chanson[]} listeChansons Nouvelle liste de chansons pour cette
     *     liste de lecture
     */
    changerListe(index, listeChansons) {
        this.listesLecture[index].chansons = listeChansons;
    }

    /**
     * Supprimer une liste de lecture de la bibliothque. Si elle n'existe pas,
     * aucune erreur n'est retourne et aucun changement n'est apport  la
     * bibliothque.
     * @param {ListeLecture} liste Liste de lecture  supprimer.
     */
    supprimerListe(liste) {
        const index = this.listesLecture.indexOf(liste);

        // Si la liste de lecture a t trouve dans la bibliothque, la
        // supprimer.
        if (index !== -1) {
            this.listesLecture.splice(index, 1);
        }
    }
}